import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {

rgb: RGB = new RGB(0,0,255);  
hsv: HSV = this.rgb2hsv(this.rgb);
cmyk: CMYK = this.rgb2cmyk(this.rgb) ;
ycbcr: YCbCr = this.rgb2ycbcr(this.rgb);



rgb2hsv(rgb: RGB) :HSV{
  let r = rgb.r;
  let g = rgb.g;
  let b = rgb.b;
  r /= 255, g /= 255, b /= 255;

  var max = Math.max(r, g, b), min = Math.min(r, g, b);
  var h, s, v = max;

  var d = max - min;
  s = max == 0 ? 0 : d / max;

  if (max == min) {
    h = 0; 
  } else {
    switch (max) {
      case r: h = (g - b) / d + (g < b ? 6 : 0); break;
      case g: h = (b - r) / d + 2; break;
      case b: h = (r - g) / d + 4; break;
    }

    h /= 6;
  }

  return new HSV(Math.round(h*360), Math.round(s*100), Math.round(v*100) );
}


hsvToRgb(hsv: HSV): RGB {
  let h = hsv.h/360, s = hsv.s/100, v = hsv.v/100;
  let r, g, b;

  let i = Math.floor(h * 6);
  let f = h * 6 - i;
  let p = v * (1 - s);
  let q = v * (1 - f * s);
  let t = v * (1 - (1 - f) * s);

  switch (i % 6) {
    case 0: r = v, g = t, b = p; break;
    case 1: r = q, g = v, b = p; break;
    case 2: r = p, g = v, b = t; break;
    case 3: r = p, g = q, b = v; break;
    case 4: r = t, g = p, b = v; break;
    case 5: r = v, g = p, b = q; break;
  }

  return new RGB( Math.round(r * 255), Math.round(g * 255), Math.round(b * 255) );
}

rgb2cmyk(rgb: RGB): CMYK {
  let r = rgb.r;
  let g = rgb.g;
  let b = rgb.b;
  if (r==0 && g==0 && b==0) {
    let computedK = 1;
    return new CMYK(0,0,0,1);
   }
  
   let computedC = 1 - (r/255);
   let computedM = 1 - (g/255);
   let computedY = 1 - (b/255);
  
   var minCMY = Math.min(computedC,
                Math.min(computedM,computedY));
   computedC = (computedC - minCMY) / (1 - minCMY) ;
   computedM = (computedM - minCMY) / (1 - minCMY) ;
   computedY = (computedY - minCMY) / (1 - minCMY) ;
   let computedK = minCMY;
  
   return new CMYK(Math.round(100*computedC),Math.round(100*computedM),Math.round(100*computedY),Math.round(100*computedK));
}

cmyk2rgb(cmyk: CMYK): RGB {
  let c = cmyk.c;
  let m = cmyk.m;
  let y = cmyk.y;
  let k = cmyk.k;

    c = (c / 100);
    m = (m / 100);
    y = (y / 100);
    k = (k / 100);
    
    c = c * (1 - k) + k;
    m = m * (1 - k) + k;
    y = y * (1 - k) + k;
    
    var r = 1 - c;
    var g = 1 - m;
    var b = 1 - y;
  
    
    return new RGB(Math.round(255*r), Math.round(255*g), Math.round(255*b));
}

rgb2ycbcr(rgb: RGB): YCbCr {
  let r = rgb.r;
  let g = rgb.g;
  let b = rgb.b;

  r /= 255;
  g /= 255;
  b /= 255;

  // let y = 16 + (65.482 * r + 128.553 * g + 24.966 * b);
  // let cb = 128 + (-37.797 * r - 74.203 * g + 112 * b);
  // let cr = 128 + (112 * r - 93.786 * g - 18.214 * b);

  // let y = 16 + 65.738*r/256 + 129.057*g/256 + 25.064*b/256;
  // let cb = 128 - 37.945*r/256 - 74.494*g/256 + 112.439*b/256;
  // let cr = 128+112.439*r - 94.154*g/256 - 18.285*b/256;

  // let y = 77/256*r+150/256*g+29/256*b;
  // let cb = -44/256*r-87/256*g+131/256*b+128;
  // let cr = 131/256*r-110/256*g-21/256*b+128;

  const A = 0.2627;
  const B = 0.6780;
  const C = 0.0593;
  const D = 1.8814;
  const E = 1.4747;

  let y = A*r+B*g+C*b;
  let cb = (b - y) / D;
  let cr = (r - y) / E;


  return new YCbCr(Math.round(y*100)/100, Math.round(cb*100)/100, Math.round(cr*100)/100);

}

ycbcr2rgb(ycbcr: YCbCr): RGB {

  let y = ycbcr.y;
  let cr = ycbcr.cr;
  let cb = ycbcr.cb;

  // let r = (298.082 * y + 408.583 * cr ) / 256 - 222.921;
  // let g = (298.082 * y - 100.291 * cb - 208.12 * cr) / 256 + 135.576;
  // let b = (298.082 * y + 516.412 * cb) / 256 - 276.836;

  // let r = y+1.371*(cr-128);
  // let g = y - 0.698*(cr-128)-0.336*(cb-128);
  // let b = y + 1.732*(cb-128);

  const A = 0.2627;
  const B = 0.6780;
  const C = 0.0593;
  const D = 1.8814;
  const E = 1.4747;

  let r = y + E * cr;
  let g = y - (A * E / B) * cr - ( C * D / B) * cb;
  let b = y + D * cb;

  return new RGB(Math.round(r*255), Math.round(g*255), Math.round(b*255));

}


 

onRgbChanged() {
  this.hsv = this.rgb2hsv(this.rgb);
  this.cmyk = this.rgb2cmyk(this.rgb);
  this.ycbcr = this.rgb2ycbcr(this.rgb);
}

onHsvChanged() {
  this.rgb = this.hsvToRgb(this.hsv);
  this.cmyk = this.rgb2cmyk(this.rgb);
  this.ycbcr = this.rgb2ycbcr(this.rgb);
}

onCmykChanged() {
  this.rgb = this.cmyk2rgb(this.cmyk);
  this.hsv = this.rgb2hsv(this.rgb);
  this.ycbcr = this.rgb2ycbcr(this.rgb);
}

onYCbCrChanged() {
  this.rgb = this.ycbcr2rgb(this.ycbcr);
  this.hsv = this.rgb2hsv(this.rgb);
  this.cmyk = this.rgb2cmyk(this.rgb);
}


}


class RGB {
  r: number;
  g: number;
  b: number;

  constructor(r, g, b) {
    this.r = r;
    this.g = g;
    this.b = b;
  }
}

class HSV {
  h: number;
  s: number;
  v: number;

  constructor(h, s, v) {
    this.h = h;
    this.s = s;
    this.v = v;
  }
}

class CMYK {
  c: number;
  m: number;
  y: number;
  k: number;

 constructor(c, m, y, k) {
    this.c = c;
    this.m = m;
    this.y = y;
    this.k = k;
  }
}

class YCbCr {
  y: number;
  cr: number;
  cb: number;

  constructor(y, cb, cr) {
    this.y = y;
    this.cr = cr;
    this.cb = cb;
  }
}
